export async function getGroupedOffers(buyboxStatus, status) {
    try {
        const guid = JSON.parse(window.localStorage.getItem("price.beater")).guid;
        if (guid == null) {
            return null;
        }

        const buyboxParam = buyboxStatus != null ? "&buyboxStatus=" + buyboxStatus : "";
        const statusParam = status != null ? "&tatus=" + status : "";

        const response = await axios.get(apiUrlJs + "/offers/buyboxstatus?guid=" + guid + buyboxParam + statusParam, {
            headers: { "x-api-key": awsKey },
        });

        const prodList = [];
        response.data.map((item, key) => {
            prodList.push(new Product(item));
        });
        return prodList;
    } catch (error) {
        console.log(error);
        return null;
    }
}

export function whoami(guid) {
    try {
        if (guid == null) {
            return null;
        }

        const response = axios.get(apiUrlPy + "/whoami?guid=" + guid, {
            headers: { "x-api-key": awsKey },
        });

        response.data.map((item, key) => {
            return new User(item);
        });

        return null;
    } catch (error) {
        console.log(error);
        return null;
    }
}
